.table {
  .cellWrapper{
    display: flex;
    align-items: center;

    img {
      width: 32px;
      height: 32px;
      margin-right: 10px;
      // object-fit: cover;
    }
  }

  .status{
    padding: 5px;
    border-radius: 5px;

    &.Approved {
      color: green;
      background-color: aquamarine;
    }
  
    &.Pending {
      color: goldenrod;
      background-color:rgba(189 , 189,3, 0.103) ;
    }
  }

  

}