.datatable {
  height: 600px;
  padding: 20px;

  .datatableTitle {
    width: 100%;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .link {
      text-decoration: none;
      color: green;
      font-size: 16px;
      font-weight: normal;
      border: 1px solid green;
      padding: 5px;
      border-radius: 5px;

      &:hover {
        background-color: rgb(176, 236, 196);
      }
    }
  }

  .cellWithImg {
    display: flex;
    align-items: center;
  
    .cellImg {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      object-fit: cover;
      margin-right: 20px;
    }
  }

  .cellWithStatus {
    padding: 5px;
    border-radius: 5px;

    &.active {
      background-color: rgba(0, 128, 0, 0.05);
      color: green;
    }

    &.pending {
      background-color: rgba(255, 217, 0, 0.05);
      color: yellow;
    }

    &.passive {
      background-color: rgba(255, 0, 0, 0.05);
      color: red;
    }
  }

  .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;

    .viewButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: darkblue;
      border: 1px dotted rgba(0, 0, 139, 0.596) ;
      cursor: pointer;

      &:hover {
        background-color: rgb(119, 175, 231);
      }
    }

    .deleteButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: crimson;
      border: 1px dotted rgba(104, 15, 15, 0.596) ;
      cursor: pointer;

      &:hover {
        background-color: rgb(239, 137, 137);
      }
    }
  }
}